'use strict';

// MODULES //

import isObject from '@stdlib/assert-is-plain-object' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import { isPrimitive as isString } from '@stdlib/assert-is-string' ;
import dtypes from '@stdlib/array-typed-dtypes' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;
import contains from './contains.js' ;


// VARIABLES //

var DTYPES = dtypes();


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {string} [options.dtype] - default data type
* @param {string} [options.name] - tuple name
* @returns {(Error|null)} null or an error object
*
* @example
* var opts = {};
* var options = {
*     'dtype': 'float64',
*     'name': 'Complex128'
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '0kl2h', options ) );
	}
	if ( hasOwnProp( options, 'dtype' ) ) {
		opts.dtype = options.dtype;
		if ( !isString( opts.dtype ) ) {
			return new TypeError( format( '0kl2i', 'dtype', opts.dtype ) );
		}
		if ( !contains( DTYPES, opts.dtype ) ) {
			return new TypeError( format( '0kl5O', 'dtype', opts.dtype ) );
		}
	}
	if ( hasOwnProp( options, 'name' ) ) {
		opts.name = options.name;
		if ( !isString( opts.name ) ) {
			return new TypeError( format( '0kl2i', 'name', opts.name ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
